.instructions__task {
  font-family: 'YS Text';
  font-style: normal;
  font-weight: 400;
  font-size: calc(10px + 10 *(100vw / 1400));
  line-height: 150%;
  max-width: 550px;
}

.instructions__task::marker {
  font-size: 14px;
}

@media screen and (min-width:1440px){
  .instructions__task {
    font-size: 20px;
  }
}
